<template>
  <div>
    <van-search
      v-model="SearchVal"
      disabled
      shape="round"
      background="#4fc08d"
      placeholder="请输入搜索关键词"
      @click="goToPopup"
    />
    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="darkred">
      <van-swipe-item v-for="item in banner" :key="item.id">
        <img :src="item.image_url" width="100%" style="display: block" alt="" />
        <!-- <h4>{{ item.name }}</h4> -->
        <!-- 价格 -->
        <div>{{ item.floor_price }}</div>
      </van-swipe-item>
    </van-swipe>
    <!--宫格 -->
    <van-grid :column-num="5">
      <van-grid-item
        v-for="item in channel"
        :key="item.id"
        :icon="item.icon_url"
        :text="item.name"
      />
    </van-grid>
    <!-- 品牌供应商 -->
    <div class="title">品牌供应商</div>
    <!-- brand -->
    <!-- 居中 -->
    <van-row type="flex" justify="center">
      <van-col span="12" v-for="item in brandList" :key="item.id">
        <img :src="item.pic_url" alt="" />
        <h4>{{ item.name }}</h4>
        <!-- 价格 -->
        <div>{{ item.floor_price | filterMoney }}</div>
      </van-col>
    </van-row>

    <!-- vant 内置样式动画 -->
    <transition name="van-slide-right">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>

import { GetHomeData } from '@/request/api'
export default {
  data() {
    return {
      SearchVal: '',
      // 轮播图数组
      banner: [],
      // 首页宫格
      channel: [],
      // 品牌制造商直供
      brandList: []
    }
  },
  created() {
    //获取首页数据
    GetHomeData().then(res => {
      if (res.errno == 0) {
        // 成功获取到数据
        console.log(res.data)
        let { banner, channel, brandList } = res.data
        this.banner = banner
        this.channel = channel
        this.brandList = brandList
      }
    })
      .catch(err => {
        console.log(err)
      })
  },
  methods: {
    //  跳转路由
    goToPopup() {
      this.$router.push('/home/popup')
    }
  }

};

</script>

