import Vue from 'vue'
import VueRouter from 'vue-router'

// 引入需要的组件
import Hellow from '@/components/Hellow.vue'
// import Nav from '@/components/nav.vue'
Vue.use(VueRouter);

// 创建路由的实例对象
const router = new VueRouter({

  routes: [
    { path: '/', redirect: '/home' },

    {
      path: '/home', name: 'Home', component: Hellow,
      meta: {
        activeNum: 4,
        isShowTabbar: true
      },

      children: [
        { path: 'popup', component: () => import('@/components/MyPopup.vue') }
      ]
    },
    // 专题路由
    {
      path: '/topice', name: 'Topice',

      component: () => import('@/views/Topice.vue')

    },

    // 分类路由
    {
      path: '/category', name: 'Category',

      component: () => import('@/views/Category.vue')

    },

    // 购物车路由
    {
      path: '/cart', name: 'Cart',

      component: () => import('@/views/Cart.vue')

    },


    // 我的路由
    {
      path: '/user', name: 'User',

      component: () => import('@/views/User.vue')

    },
    {
      path: '/productDetail', name: 'ProductDetail',

      component: () => import('@/views/ProductDetail.vue')

    },

  ]

})

// 全局前置守卫

router.beforeEach((to, from, next) => {
  let token = localStorage.getItem('token')
  if (to.path == "/cart") {
    if (token) {
      next();
    } else {
      // alert('请先登录')
      Vue.prototype.$toast('请先登录')
      // 定时器
      setTimeout(() => {
        next("/user")
      }, 1000)
    }
    return;
  }
  next()
})


export default router